import React, {useState} from 'react'

export default function TextForm(props) {
   const [text, setText] = useState('');
   const touppercase =()=>{
      setText(text.toUpperCase())
   }
   const tolowercase =()=>{
      setText(text.toLowerCase())
   }
   const handleChange =(e)=>{
      setText(e.target.value)
   }
   const clearText = (e) =>{
      setText('')
   }
   const copyText = (e) =>{
      var txt = document.getElementById("textarea1")
      txt.select();
      navigator.clipboard.writeText(txt.value)
   }
   const extraSpaces = (e) => {
      setText(text.split(/[ ]+/).join(' '))
   }
   return (
      <>
      <div className={`row ${props.modeCol}`}>
         <div className="col s10">
         <div className="row">
            <div className="input-field col s12">
               <textarea onChange={handleChange} value={text} id="textarea1" className={`materialize-textarea ${props.modeCol}`}></textarea>
               <label htmlFor="textarea1">{props.heading}</label>
            </div>
            <button onClick={touppercase} className="waves-effect waves-light btn blue darken-1 mx-1">Uppercase</button>
            <button onClick={tolowercase} className="waves-effect waves-light btn blue darken-1 mx-1">Lowercase</button>
            <button onClick={clearText} className="waves-effect waves-light btn blue darken-1 mx-1">Clear</button>
            <button onClick={copyText} className="waves-effect waves-light btn blue darken-1 mx-1">Copy</button>
            <button onClick={extraSpaces} className="waves-effect waves-light btn blue darken-1 mx-1">Remove Extra Spaces</button>
         </div>
         </div>
         <div className="col s2">
         <div className="row">
            <h4>Your Text Summary</h4>
            <p><b>{text.split(' ').length}</b> Words</p>
            <p><b>{text.length}</b> Characters</p>
            <p>{0.008 * text.split(' ').length} min reading time</p>
         </div>
         </div>
      </div>
      <div className={`row ${props.modeCol}`}>
         <h4>Preview</h4>
         <p>{text===''?'Start by entering your text':text}</p>
      </div>
      </>
   )
}
