import { useState } from 'react';

import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";

import './App.css';
import About from './components/About';
import Alert from './components/Alert';
import Navbar from './components/Navbar';
import TextForm from './components/TextForm';
function App() {
  const [mode, setMode] = useState('blue');
  const [modeCol, setmodeCol] = useState('black-text')
  const [alert, setalert] = useState(null)
  const toggleMode = () => {
    if(mode==='blue'){
      setMode('black')
      setmodeCol('grey-text')
      document.body.style.backgroundColor = '#212121';
      alertMsg('Dark Mode has been enabled', 'success')
      document.title = 'Text Util - Dark Mode';
    }else{
      setMode('blue')
      setmodeCol('black-text')
      document.body.style.backgroundColor = '#fff';
      alertMsg('Light Mode has been enabled', 'success')
      document.title = 'Text Util - Light Mode';
    }
  }
  const alertMsg = (message, type) => {
    setalert({
      msg: message,
      type: type
    })
  }

  return(
    <>
      <Navbar title="Text Utils" mode={mode} modeCol={modeCol} toggleMode={toggleMode}></Navbar>
      <Alert modeCol={modeCol} alert={alert}/>
      <div className="container">
        <Router>
          <Switch>
            <Route exact path="/About">
              <About modeCol={modeCol}  mode={mode}/>
            </Route>
            <Route exact path="/">
              <TextForm heading="Enter text to utilize" modeCol={modeCol}/>
            </Route>
          </Switch>
        </Router>
      </div>
    </>
  );
}

export default App;
