import React from 'react'
import PropTypes from 'prop-types'
import { BrowserRouter, Link } from 'react-router-dom'

export default function Navbar(props) {
   return (
      <>
         <nav className={`${props.mode} darken-1`}>
         <BrowserRouter>
            <div className="nav-wrapper">
               <a href="/" className="center brand-logo">{props.title}</a>
               <Link to="/" data-target="mobile-demo" className="sidenav-trigger"><i className="material-icons">menu</i></Link>
               <ul className="right hide-on-med-and-down">
                  <li>
                     <div className="switch">
                        <label>
                           <span className={props.modeCol}>Light</span>
                           <input type="checkbox" onChange={props.toggleMode}/>
                           <span className="lever purple-text"></span>
                           <span className={props.modeCol}>Dark</span>
                        </label>
                     </div>
                  </li>
                  <li><Link to="/">Home</Link></li>
                  <li><Link to="/About">About</Link></li>
                  <li><Link to="/Contact">Contact</Link></li>
               </ul>
            </div>
            </BrowserRouter>
         </nav>
      </>
   )
}

Navbar.propTypes = {title: PropTypes.string.isRequired}
Navbar.defaultProps = {title: 'TextUtils'}